<template lang="pug">
#works
  .head
    .top
      .hollow(data-aos="fade", data-aos-duration="800") WORKS
      .container(
        data-aos="fade",
        data-aos-duration="800",
        data-aos-delay="500"
      )
        nav.d-flex.i_pl_4
          .sheet(
            @click="sheetSelected(index, sheet.pageName)",
            v-for="(sheet, index) in sheets",
            :class="{ selected: sheetSelectedIndex == index }"
          ) {{ sheet.title }}
        h2.title WORKS
          span {{ isCustomized ? pageTitle[1] : pageTitle[0] }}
        .filter.fade.flex-column-reverse.flex-md-row.justify-content-md-between(
          v-if="isCustomized"
        )
          .btns.d-flex.justify-content-between.justify-content-md-start
            .type_btn(
              @click="onTypeBtnClick(0)",
              :class="{ selected: selectedIndex == 0 }"
            )
              p 依平台類型
                font-awesome-icon.i_ml_1(:icon="['fas', 'angle-down']")
            .type_btn(
              @click="onTypeBtnClick(1)",
              :class="{ selected: selectedIndex == 1 }"
            )
              p 依專案類型
                font-awesome-icon.i_ml_1(:icon="['fas', 'angle-down']")
            .type_btn(
              @click="onTypeBtnClick(2)",
              :class="{ selected: selectedIndex == 2 }"
            )
              p 依功能類型
                font-awesome-icon.i_ml_1(:icon="['fas', 'angle-down']")
          .filter_btn.text-right(
            @click="onFilterBtnClick",
            :class="{ on: isFilterOn }"
          ) 篩選
            font-awesome-icon.i_ml_1(:icon="['fas', 'filter']")
    .bottom(ref="bottom", v-if="isCustomized")
      .container
        .filter_items
          filter-item.fade(
            v-for="(item, index) in currentFilterList",
            :text="item",
            :key="index"
          )
        .clear_btn 清空類別
          font-awesome-icon.i_ml_1(:icon="['fas', 'trash']")

  transition(name="page", mode="out-in")
    router-view
</template>

<script>
import FilterItem from "@/components/works/FilterItem";
import Velocity from "velocity-animate";

export default {
  name: "Works",
  components: {
    "filter-item": FilterItem,
  },
  data() {
    return {
      type: this.$route.query.type,
      bottomScrollHeight: 0,
      isCustomized: false,
      sheetSelectedIndex: 0,
      selectedIndex: -1,
      isFilterOn: true,
      pageTitle: ["作品- 創新之光(服務)", "作品- 客製化服務"],
      sheets: [
        { title: "方案作品", pageName: "WorksSolution" },
        { title: "客製化作品", pageName: "WorksCustomized" },
      ],
      platform: ["網頁前端", "Android", "iOS", "後台"],
      project: [
        "即時媒合",
        "人事業務管理",
        "品牌經營",
        "社群互動",
        "其他",
        "IOT",
      ],
      function: [
        "會員功能",
        "儀表板",
        "社群功能",
        "會員行銷",
        "購物車",
        "事件推播",
      ],
      currentFilterList: [],
    };
  },
  created() {
    this.switchPageSheet();
  },
  mounted() {
    this.getCurrentPagePath();
    this.$nextTick(() => {
      this.getCurrentPagePath();
    });
  },
  methods: {
    switchPageSheet() {
      if ("WorksCustomized" == this.type) {
        this.sheetSelected(1, this.type);
      } else {
        this.sheetSelected(0, this.type);
      }
    },
    expand(isOpen) {
      if (isOpen) {
        var height;
        var bottom = this.$refs.bottom;

        this.$nextTick(() => {
          //取得元素完整高度
          height = bottom.scrollHeight;

          Velocity(bottom, { maxHeight: height }, { duration: 200 });
        });
      } else {
        Velocity(this.$refs.bottom, { maxHeight: 0 }, { duration: 200 });
      }
    },
    onTypeBtnClick(index) {
      this.selectedIndex = this.selectedIndex == index ? -1 : index;
      this.setFilterList(index);

      if (this.selectedIndex > -1) {
        this.isFilterOn = true;
        this.expand(true);
      } else {
        this.expand(false);
      }
    },
    onFilterBtnClick() {
      this.isFilterOn = !this.isFilterOn;
      if (!this.isFilterOn) {
        this.selectedIndex = -1;
      }
    },
    setFilterList(index) {
      switch (index) {
        case 0:
          this.currentFilterList = this.platform;
          break;
        case 1:
          this.currentFilterList = this.project;
          break;
        case 2:
          this.currentFilterList = this.function;
          break;
      }
    },
    sheetSelected(index, pageName) {
      this.sheetSelectedIndex = index;
      this.toPage(pageName);

      if (pageName == "WorksSolution") {
        this.isCustomized = false;
      } else {
        this.isCustomized = true;
      }
    },
    toPage(pageName) {
      this.$router.push({ name: pageName });
    },
    getCurrentPagePath() {
      if (this.$route.path.indexOf("solution") != -1) {
        this.isCustomized = false;
        this.sheetSelectedIndex = 0;
      } else {
        this.isCustomized = true;
        this.sheetSelectedIndex = 1;
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "@/assets/style/works/works.scss";
</style>
